.section__left {
  flex: 1 1 49%;
}

.section__right {
  flex: 1 1 51%;
}

.section-info {
  padding-top: 220px;
  padding-left: 150px;
  background-color: #fff;
  height: 505px;
  color: #1d2f39;
  font-weight: 600;
}

.section-info__description {
  font-size: 16px;
  line-height: 19px;
  opacity: 0.3;
  margin-bottom: 18px;
}

.section-info__title {
  font-size: 32px;
  line-height: 38px;
  max-width: 385px;
  letter-spacing: 1px;
  font-weight: 700;
}

.section-title {
  background-color: #f0f0f0;
  padding-left: 150px;
  padding-top: 72px;
  min-height: 395px;
  position: relative;
}

.section-title__img {
  position: absolute;
}

.section-title__img.tree {
  bottom: 0;
  right: 0;
}

.section-title__img.rock {
  top: -90px;
  right: 0;
}

.section-title__img.skin {
  right: 0;
  top: -225px;
}

.section-title__main {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  color: #1d2f39;
  margin-bottom: 8px;
}

.section-title__description {
  font-size: 10px;
  line-height: 12px;
  color: #8c9da7;
  max-width: 180px;
  position: relative;
}

.section-title__description::after {
  content: "";
  width: 24px;
  height: 12px;
  position: absolute;
  bottom: -24px;
  left: 0;
  background-image: url(../../img/arrow-right.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.section-title--right {
  padding-top: 200px;
  padding-left: 70px;
}

.section-number {
  background-color: #fafafa;
}

.section-number__square {
  width: 192px;
  height: 192px;
  padding: 43px 32px;
}

.section-number__title {
  font-weight: bold;
  font-size: 48px;
  line-height: 58px;
  color: #ffffff;
  opacity: 0.2;
}

.section-number__descr {
  font-size: 8px;
  line-height: 9px;
  color: #ffffff;
  font-weight: 300;
  opacity: 0.8;
}

.section-img {
  min-height: 708px;
}

.section-img img {
  height: 100%;
  object-fit: cover;
}

.square__one {
  background-color: #1d2f39;
}

.square__two {
  background-color: #ddb77f;
  margin-left: auto;
}

.square__tree {
  background-color: #b95955;
}

.square__four {
  background-color: #535a63;
  margin-left: auto;
}

.section-main {
  display: flex;
}
